import { createNewPaymentLink } from '@api/premium/createNewPaymentLink';
import { getCreateNewPaymentLinkUrl } from '@api/premium/urls';
import { getSessionMetaApiData } from '@app/storage/session/metaApiData';
import {
  getLocalStoragePremiumData,
  LocalStoragePremiumKey,
} from '@app/storage/local/premium';
import { PaymentMethod } from '@app/storage/session/shared';
import { Tariff } from '@api/premium/types/Tariff';

export function useNewPaymentLink() {
  async function getNewPaymentLink(
    paymentMethod: PaymentMethod,
    tariffName: Tariff,
  ) {
    const { apiBaseUrl } = await getSessionMetaApiData();
    const { userId } = await getLocalStoragePremiumData([
      LocalStoragePremiumKey.UserId,
    ]);
    const url = getCreateNewPaymentLinkUrl(apiBaseUrl);
    const data = await createNewPaymentLink({
      url,
      userId,
      tariffName,
      paymentMethod,
    });

    return data;
  }
  return getNewPaymentLink;
}
