import { LocalStoragePremiumKey } from '@app/storage/local/premium';
import { type PaymentStatus } from '@app/storage/local/premium';
import { useLocalStorage } from './useLocalStorage';

export function usePaymentStatus(): PaymentStatus | undefined {
  const [paymentStatus] = useLocalStorage(LocalStoragePremiumKey.Status);

  return paymentStatus ?? undefined;
}
