import {
  LocalStoragePremiumData,
  LocalStoragePremiumKey,
} from '@app/storage/local/premium';
import { LocalStorageKey, LocalStorageData } from '@app/storage/local/shared';
import { useState, useEffect } from 'react';

type StorageValue<K> = K extends LocalStorageKey
  ? LocalStorageData[K]
  : K extends LocalStoragePremiumKey
    ? LocalStoragePremiumData[K]
    : never;

export function useLocalStorage<
  K extends LocalStorageKey | LocalStoragePremiumKey,
>(key: K | K[], defaultValue?: StorageValue<K>) {
  const [value, setValue] = useState<StorageValue<K> | null>(
    defaultValue ?? null,
  );
  const storageKey = Array.isArray(key) ? key[0] : key;

  useEffect(() => {
    // Initial value
    chrome.storage.local.get(storageKey, (result) => {
      setValue(result[storageKey] as StorageValue<K>);
    });

    // Listen for changes
    const listener = (changes: {
      [key: string]: chrome.storage.StorageChange;
    }) => {
      if (changes[storageKey]) {
        setValue(changes[storageKey].newValue as StorageValue<K>);
      }
    };

    chrome.storage.local.onChanged.addListener(listener);

    return () => {
      chrome.storage.local.onChanged.removeListener(listener);
    };
  }, [storageKey]);

  const set = async (newValue: StorageValue<K>) => {
    await chrome.storage.local.set({ [storageKey]: newValue });
  };

  return [value, set] as const;
}
