import { Actions } from '@app/actions/shared';
import {
  SubscriptionInfoError,
  fetchSubscriptionInfo,
} from '../../../api/premium/fetchPremiumData';
import {
  DeviceLimit,
  GetSubscriptionResponse,
} from '../../../api/premium/urls';
import { getSessionMetaApiData } from '../session/metaApiData';
import { setSessionStorageData } from '../session/shared';
import {
  LocalStorageKey,
  getLocalStorageData,
  localStorageScope,
  setLocalStorageData,
} from './shared';
import { getDeviceId, getDeviceIP } from '@background/client/utils';
import { getIsPremiumStatus } from '@app/utils/getIsPremiumStatus';
import { Tariff } from '@api/premium/types/Tariff';

export enum LocalStoragePremiumKey {
  UserId = 'userId',
  DaysLeft = 'daysLeft',
  HasPremium = 'hasPremium',
  PaidUntil = 'paidUntil',
  SecondsLeft = 'secondsLeft',
  Status = 'status',
  IsSubscriptionInfoError = 'isSubscriptionInfoError',
  IsChangeDeviceError = 'isChangeDeviceError',
  IsAdBannerShown = 'isAdBannerShown',
  IsAllInternetProxy = 'isAllInternetProxy',
  PACPremiumData = 'pacPremiumData',
  IsFreePremiumTrialAvailable = 'isFreePremiumTrialAvailable',
  CurrentTariff = 'currentTariff',
  DeviceLimit = 'deviceLimit',
  ActiveDevices = 'activeDevices',
}

export type PaymentStatus = 'New' | 'Paid' | 'Expired' | 'Trial' | 'Free';

export type PACPremiumData = {
  premium: string;
  allInternet: string;
};

export type LocalStoragePremiumData = {
  userId?: string;
  daysLeft?: number;
  hasPremium?: boolean;
  paidUntil?: string;
  secondsLeft?: number;
  status?: PaymentStatus;
  isSubscriptionInfoError?: boolean;
  isChangeDeviceError?: boolean;
  isAdBannerShown?: boolean;
  isAllInternetProxy?: boolean;
  pacPremiumData?: PACPremiumData;
  isFreePremiumTrialAvailable?: boolean;
  currentTariff: Tariff;
  deviceLimit: DeviceLimit;
  activeDevices: number;
};

export async function getLocalStoragePremiumData<
  Key extends LocalStoragePremiumKey,
>(
  keys: Key[] | Key,
): Promise<
  | {
      [P in Key[][number]]: LocalStoragePremiumData[P];
    }
  | LocalStoragePremiumData
> {
  if (!Array.isArray(keys)) {
    return chrome.storage.local.get(keys) as Promise<{
      [P in Key[][number]]: LocalStoragePremiumData[P];
    }>;
  }
  return chrome.storage.local.get(keys) as Promise<LocalStoragePremiumData>;
}

export async function setLocalStoragePremiumData(
  data: Partial<LocalStoragePremiumData>,
): Promise<void> {
  await chrome.storage.local.set(data);
}

export async function deleteLocalStoragePremiumData(
  key: LocalStoragePremiumKey | LocalStoragePremiumKey[],
): Promise<void> {
  await chrome.storage.local.remove(key);
}

function isGetSubscriptionInfoError(
  data: GetSubscriptionResponse | SubscriptionInfoError,
): data is SubscriptionInfoError {
  return 'error' in data && data.error !== null;
}

export async function getSubscriptionInfo(): Promise<GetSubscriptionResponse | null> {
  if (process.env.EXTENSION_CODE === 'naruzhuv2') {
    return null;
  }

  const isOnlyPremium: boolean = process.env.IS_ONLY_PREMIUM === 'true';

  try {
    await setLocalStoragePremiumData({ isSubscriptionInfoError: false });

    const { apiBaseUrl } = await getSessionMetaApiData();

    const deviceId = await getDeviceId();
    const deviceIp = await getDeviceIP();

    const { userId } = await getLocalStoragePremiumData(
      LocalStoragePremiumKey.UserId,
    );

    if (!userId) {
      localStorageScope.captureException(
        new Error('User ID is empty when fetching subscription info'),
      );
      return null;
    }

    const subscriptionInfo = await fetchSubscriptionInfo(apiBaseUrl, {
      userId,
      deviceId,
      deviceIp,
    });

    if (isGetSubscriptionInfoError(subscriptionInfo)) {
      if (subscriptionInfo.error === 'changeDevice') {
        await chrome.runtime.sendMessage({
          action: Actions.OpenPopupChangeDevice,
        });

        await setLocalStoragePremiumData({ isChangeDeviceError: true });
        await setLocalStorageData({ targetState: 'disconnected' });
        return null;
      }

      if (subscriptionInfo.error === 'requestBlockedError') {
        await setSessionStorageData({
          requestBlockedError:
            'request fetchSubscriptionInfo is probably blocked',
        });
      }

      throw new Error(subscriptionInfo.error);
    }

    const { daysLeft, paidUntil, status, secondsLeft } =
      subscriptionInfo?.data?.subscription ?? {};

    const {
      name: currentTariff,
      deviceLimit,
      activeDevices,
    } = subscriptionInfo?.data?.tariff ?? {};

    const isPremiumStatus = getIsPremiumStatus(status);

    await setLocalStoragePremiumData({
      daysLeft,
      paidUntil,
      status,
      secondsLeft,
      currentTariff,
      deviceLimit,
      activeDevices,
    });

    const { hasPremium } = await getLocalStoragePremiumData(
      LocalStoragePremiumKey.HasPremium,
    );

    const { currentState } = await getLocalStorageData(
      LocalStorageKey.CurrentState,
    );

    await setLocalStoragePremiumData({
      hasPremium: isOnlyPremium || isPremiumStatus,
    });

    if (status === 'Expired' && hasPremium && currentState === 'connected') {
      await setLocalStorageData({ targetState: 'disconnected' });
    }

    return subscriptionInfo;
  } catch (error) {
    console.error(error);

    if (error instanceof Error && error.message === 'userNotFound') {
      throw error;
    }

    await setLocalStoragePremiumData({ isSubscriptionInfoError: true });
    return null;
  }
}
