import dayjs, { extend } from 'dayjs';
import utc from 'dayjs/plugin/utc';
import ky, { HTTPError, type KyRequest, type KyResponse } from 'ky';
import {
  SessionStorageKey,
  getSessionStorageData,
  setSessionStorageData,
} from '../app/storage/session/shared';
import { createSentryScope } from '@app/sentry/createSentryScope';
import { wait, getFibonacciNumber } from 'src/utils';

export const apiScope = createSentryScope('api');

const limitInSeconds = 60 * 60;

const getTimingWithLimit = (timingInSec: number, limitInSec: number) => {
  if (timingInSec > limitInSec) {
    return limitInSec;
  }
  return timingInSec;
};

const getExtensionVersion = (): string => {
  return chrome.runtime.getManifest().version;
};

const createCustomHeaders = (): Record<string, string> => {
  return {
    'X-Client-Version': getExtensionVersion(),
  };
};

extend(utc);

export const httpClient = ky.extend({
  retry: {
    limit: 30,
    methods: ['get', 'post'],
  },
  hooks: {
    beforeRequest: [
      async (request) => {
        const { metaApiData } = await getSessionStorageData(
          SessionStorageKey.MetaApiData,
        );

        const apiUrl = metaApiData?.apiBaseUrl;
        const cookieDomain = metaApiData?.cookieDomain;

        const url = new URL(request.url);
        if (
          apiUrl &&
          cookieDomain &&
          (url.hostname.includes(apiUrl) || url.hostname.includes(cookieDomain))
        ) {
          const customHeaders = createCustomHeaders();
          Object.entries(customHeaders).forEach(([key, value]) => {
            request.headers.set(key, value);
          });
        }
        return request;
      },
    ],
    beforeRetry: [
      async ({ error, retryCount }) => {
        if (!(error instanceof HTTPError)) {
          await wait(
            getTimingWithLimit(
              10 + getFibonacciNumber(retryCount),
              limitInSeconds,
            ) * 1000,
          );
          return;
        }
        const retryAfterHeader = error.response.headers.get('Retry-After');
        const retryAfterTiming = retryAfterHeader
          ? Number(retryAfterHeader)
          : NaN;
        if (!isNaN(retryAfterTiming)) {
          await wait(
            getTimingWithLimit(
              retryAfterTiming + getFibonacciNumber(retryCount),
              limitInSeconds,
            ) * 1000,
          );
          return;
        }
        await wait(
          getTimingWithLimit(
            10 + getFibonacciNumber(retryCount),
            limitInSeconds,
          ) * 1000,
        );
        return;
      },
    ],
    afterResponse: [
      async (request, options, response) => {
        if (response.ok) {
          const responseClone = response.clone() as KyResponse;
          const timestampUtc = dayjs.utc().format();

          let responseData = null;
          try {
            responseData = await responseClone.json();
          } catch (err) {
            responseData = null;
          }

          const requestLog = {
            timestampUtc,
            status: response.status,
            statusText: response.statusText,
            url: request.url,
          };
          let { apiHttpSuccess } = await getSessionStorageData(
            SessionStorageKey.ApiHttpSuccess,
          );
          if (!apiHttpSuccess) {
            apiHttpSuccess = {};
          }
          await setSessionStorageData({
            apiHttpSuccess: {
              ...apiHttpSuccess,
              [request.url]: requestLog,
            },
          });
        }
        return response;
      },
    ],
    beforeError: [
      async (error) => {
        const { request, response, message } = error;
        const responseClone = response.clone() as KyResponse;
        const requestClone = request.clone() as KyRequest;

        apiScope.setExtra('request', requestClone);
        apiScope.setExtra('response', responseClone);
        apiScope.setExtra('message', message);
        apiScope.setLevel('error');
        apiScope.setTag('request.url', requestClone.url);
        apiScope.setTag('request.method', requestClone.method);
        apiScope.setTag('response.status', responseClone.status);
        apiScope.captureException(new Error(message));

        let requestData = null;
        let errorData = null;
        try {
          requestData = await requestClone.json();
          errorData = await responseClone.json();
        } catch {
          requestData = null;
          errorData = null;
        }
        const timestampUtc = dayjs.utc().format();
        const errorLog = {
          timestampUtc,
          message,
          errorData,
          status: responseClone.status,
          statusText: responseClone.statusText,
          requestData,
        };
        let { apiHttpErrors } = await getSessionStorageData(
          SessionStorageKey.ApiHttpErrors,
        );
        if (!apiHttpErrors) {
          apiHttpErrors = {};
        }
        await setSessionStorageData({
          apiHttpErrors: {
            ...apiHttpErrors,
            [responseClone.url]: errorLog,
          },
        });
        return error;
      },
    ],
  },
});
