import { featureStore } from '@app/features/list';
import { httpClient } from '../httpClient';
import { type FetchTariffsResponse } from './urls';
import { Feature } from '@app/features/types/Feature';
import { TariffType } from './types/Tariff';

export async function fetchTariffs(url: string): Promise<FetchTariffsResponse> {
  const { featuresList } = await featureStore.getSnapshotAsync();
  const isMultiDeviceSupported = featuresList[Feature.MultiDeviceSupport];

  const data = await httpClient
    .post<FetchTariffsResponse>(url, {
      json: {
        tariffType: isMultiDeviceSupported
          ? TariffType.LimitedDevices
          : TariffType.Base,
      },
    })
    .json();
  return data;
}
