export enum Alarms {
  UpdateProxyData = 'updateProxyData',
  EnableProxy = 'enableProxy',
  DisableProxy = 'disableProxy',
  EnableProxyOnFail = 'EnableProxyOnFail',
  UpdateMetaApiData = 'updateMetaApiData',
  RestoreLitePAC = 'RestoreLitePAC',
  UpdatePacScriptToFullPAC = 'UpdatePacScriptToFullPAC',
  ExtensionMonitoring = 'extensionMonitoring',
  ExtensionConflictWarningProxy = 'extensionConflictWarningProxy',
  ExtensionConflictWarningAdBlocker = 'extensionConflictWarningAdBlocker',
}

export async function getChromeAlarm(
  name: Alarms,
): Promise<chrome.alarms.Alarm | undefined> {
  const alarm = await chrome.alarms.get(name);
  return alarm;
}
