import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  type PopupReadyMessage,
  type Message,
  type CreateNewUserSuccessMessage,
} from '../../../../app/messages/shared';
import { Actions } from '../../../../app/actions/shared';
import { RoutePath } from '../routes/types/RoutePath';

export function useNavigateToTariffs(): void {
  const navigate = useNavigate();

  useEffect(() => {
    const handleMessage = (message: Message | CreateNewUserSuccessMessage) => {
      if (
        message.action === Actions.NavigateToPayment ||
        message.action === Actions.CreateNewUserSuccess
      ) {
        const isGuestMode = !!(
          message.action === Actions.CreateNewUserSuccess &&
          message.payload?.isGuestMode
        );

        if (isGuestMode) {
          return;
        }

        navigate(RoutePath.Tariffs, {
          state: {
            hasOneMonthDiscount: message.payload?.isSaleActive ?? false,
          },
        });
      }
    };

    chrome.runtime.onMessage.addListener(handleMessage);

    return () => {
      chrome.runtime.onMessage.removeListener(handleMessage);
    };
  }, [navigate]);
  useEffect(() => {
    void chrome.runtime.sendMessage<PopupReadyMessage>({
      action: Actions.PopupReady,
    });
  });
}
