import {
  CookieKey,
  getCookieData,
  setCookieData,
} from '@app/storage/cookies/shared';
import {
  LocalStorageKey,
  getLocalStorageData,
  setLocalStorageData,
} from '../../app/storage/local/shared';

export function generateUUID(): string {
  // Generate a UUID, for example, using the uuid library if available
  // return 'xxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, (c) => {
  //   const r = (Math.random() * 16) | 0,
  //     v = c === 'x' ? r : (r & 0x3) | 0x8;
  //   return v.toString(16);
  // });
  return crypto.randomUUID();
}

async function generateDeviceId(): Promise<string> {
  const deviceId = generateUUID();

  await setCookieData({ [CookieKey.DeviceId]: deviceId });
  await setLocalStorageData({ [LocalStorageKey.DeviceId]: deviceId });

  return deviceId;
}

export async function getDeviceId(): Promise<string> {
  const { deviceId: localStorageDeviceId } = await getLocalStorageData(
    LocalStorageKey.DeviceId,
  );

  if (localStorageDeviceId) {
    return localStorageDeviceId;
  }

  const { deviceId: cookieDeviceId } = await getCookieData(CookieKey.DeviceId);

  if (cookieDeviceId) {
    await setLocalStorageData({ [LocalStorageKey.DeviceId]: cookieDeviceId });
    return cookieDeviceId;
  }

  const newDeviceId = await generateDeviceId();

  return newDeviceId;
}

/**
 * @deprecated - will remove after backend fix
 */
export async function getDeviceIP(): Promise<string | undefined> {
  try {
    const data = {
      ip: '127.0.0.1', // dummy ip
    };

    await setLocalStorageData({ [LocalStorageKey.DeviceIp]: data.ip });

    return data.ip;
  } catch (error) {
    console.warn('Error fetching public IP:', error);
  }
}
