import { type PaymentStatus } from '../../app/storage/local/premium';
import { PaymentMethod } from '../../app/storage/session/shared';
import { type Tariff } from './types/Tariff';

const PREFIX = 'premium/api/v1/uboost-premium';
const PREFIX_V2 = 'premium/api/v2/uboost-premium';
const PREFIX_V3 = 'api/v2/premium';

const CREATE_NEW_USER_API = `${PREFIX}/create-new-user`;

export type CreateNewUserResponse = {
  userId: string;
};

export type CreateNewPaymentLink = {
  userId: string;
  tariffName: string;
  paymentMethod: PaymentMethod;
};

export function getCreateNewUserApiUrl(
  apiBase: string,
): ReturnType<typeof createApiUrl<typeof CREATE_NEW_USER_API>> {
  return createApiUrl(apiBase, CREATE_NEW_USER_API);
}

const GET_SUBSCRIPTION_API = `${PREFIX_V3}/update-and-get-subscription`;

export type GetSubscriptionResponse = {
  data: {
    user: {
      public_user_id: string;
    };
    subscription: {
      status: PaymentStatus;
      paidUntil: string;
      daysLeft: number;
      secondsLeft: number;
    };
    tariff: {
      name: Tariff;
      deviceLimit: DeviceLimit;
      activeDevices: number;
    };
  };
};

export function getSubscriptionApiUrl(
  apiBase: string,
): ReturnType<typeof createApiUrl<typeof GET_SUBSCRIPTION_API>> {
  return createApiUrl(apiBase, GET_SUBSCRIPTION_API);
}

const CANCEL_SUBSCRIPTION_API = `${PREFIX}/cancel-subscription`;

export type CancelSubscriptionStatus = 'Canceled' | 'CanceledNoRefund';

export type CancelSubscriptionResponse = {
  status: CancelSubscriptionStatus;
};

export function getCancelSubscriptionApiUrl(
  apiBase: string,
): ReturnType<typeof createApiUrl<typeof CANCEL_SUBSCRIPTION_API>> {
  return createApiUrl(apiBase, CANCEL_SUBSCRIPTION_API);
}

const GET_PROXY_API = `${PREFIX_V2}/get-proxy`;

export function getProxyApiUrl(
  apiBase: string,
): ReturnType<typeof createApiUrl<typeof GET_PROXY_API>> {
  return createApiUrl(apiBase, GET_PROXY_API);
}

const CHANGE_DEVICE_API = `${PREFIX}/change-device`;

export function getChangeDeviceApiUrl(
  apiBase: string,
): ReturnType<typeof createApiUrl<typeof CHANGE_DEVICE_API>> {
  return createApiUrl(apiBase, CHANGE_DEVICE_API);
}

const CHECK_TARIFF_API = `${PREFIX}/check-tariff`;

export function getCheckTariffApiUrl(
  apiBase: string,
): ReturnType<typeof createApiUrl<typeof CHECK_TARIFF_API>> {
  return createApiUrl(apiBase, CHECK_TARIFF_API);
}

export type CheckTariffResponse = {
  status: 'allowed' | 'disallowed';
};

const SETUP_TARIFF_API = `${PREFIX}/setup-tariff`;

export function getSetupTariffApiUrl(
  apiBase: string,
): ReturnType<typeof createApiUrl<typeof SETUP_TARIFF_API>> {
  return createApiUrl(apiBase, SETUP_TARIFF_API);
}

export type SetupTariffResponse =
  | {
      status: 'done';
      paymentLink: null;
    }
  | {
      status: 'need_payment';
      paymentLink: string;
    };

const SETUP_TARIFF_FREE_API = `${PREFIX_V3}/set-free-status`;

export function getSetupTariffFreeApiUrl(
  apiBase: string,
): ReturnType<typeof createApiUrl<typeof SETUP_TARIFF_FREE_API>> {
  return createApiUrl(apiBase, SETUP_TARIFF_FREE_API);
}

export type SetupTariffFreeResponse = GetSubscriptionResponse;

const FETCH_TARIFFS_API = `${PREFIX}/get-tariffs`;

export function getFetchTariffsApiUrl(
  apiBase: string,
): ReturnType<typeof createApiUrl<typeof FETCH_TARIFFS_API>> {
  return createApiUrl(apiBase, FETCH_TARIFFS_API);
}

const CREATE_NEW_PAYMENT_LINK_API = `${PREFIX}/new-payment-link`;

export function getCreateNewPaymentLinkUrl(
  apiBase: string,
): ReturnType<typeof createApiUrl<typeof CREATE_NEW_PAYMENT_LINK_API>> {
  return createApiUrl(apiBase, CREATE_NEW_PAYMENT_LINK_API);
}

export type CreateNewPaymentLinkResponse = {
  url: string;
};

export type DeviceLimit = number;

export type TariffData = {
  tariff: Tariff;
  price: number;
  unlockedDays: number;
  period: string;
  description: null;
  monthPrice: number;
  deviceLimit: DeviceLimit;
};

export type FetchTariffsResponse = TariffData[];

function createApiUrl<T extends string>(
  apiBase: string,
  apiRoute: T,
): `https://${string}/${T}` {
  return `https://${apiBase}/${apiRoute}`;
}
