import { Button } from '@popup/shared/components/Button';
import { DebugButton } from '@popup/shared/components/DebugButton';
import styles from './Footer.module.css';
import { useTranslation } from 'react-i18next';

const SUPPORT_URL = process.env.SUPPORT_URL;

export function Footer(): JSX.Element {
  const { t } = useTranslation('youboost-neo', {
    keyPrefix: 'footer',
  });

  const version = chrome.runtime.getManifest().version;
  return (
    <div className={styles.links}>
      <span className={styles.version}>ver. {version}</span>
      <a
        href={SUPPORT_URL}
        target="_blank"
        rel="noreferrer"
        className={styles.link}
      >
        <Button variant="link">{t('support')}</Button>
      </a>
      <DebugButton />
    </div>
  );
}
