import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  LocalStoragePremiumKey,
  getLocalStoragePremiumData,
} from '@app/storage/local/premium';
import { RoutePath } from '../routes/types/RoutePath';

export function useChangeDevice(): void {
  const navigate = useNavigate();

  useEffect(() => {
    async function handleDeviceChangeError(): Promise<void> {
      const { isChangeDeviceError, userId } = await getLocalStoragePremiumData([
        LocalStoragePremiumKey.IsChangeDeviceError,
        LocalStoragePremiumKey.UserId,
      ]);
      if (isChangeDeviceError) {
        navigate(RoutePath.ChangeDevice, {
          state: { userId },
        });
      }
    }
    void handleDeviceChangeError();
  }, [navigate]);

  useEffect(() => {
    async function listener(changes: {
      isChangeDeviceError?: { newValue?: boolean };
    }): Promise<void> {
      if (changes.isChangeDeviceError?.newValue) {
        const { userId } = await getLocalStoragePremiumData([
          LocalStoragePremiumKey.UserId,
        ]);

        navigate(RoutePath.ChangeDevice, {
          state: { userId },
        });
      }
    }
    chrome.storage.onChanged.addListener(listener);
    return () => {
      chrome.storage.onChanged.removeListener(listener);
    };
  }, [navigate]);
}
