import { setupTariffFree } from '@api/premium/setupTariffFree';
import {
  LocalStoragePremiumKey,
  getLocalStoragePremiumData,
  setLocalStoragePremiumData,
} from '@app/storage/local/premium';
import { getSessionMetaApiData } from '@app/storage/session/metaApiData';
import { getDeviceIP, getDeviceId } from '@background/client/utils';
import { useState } from 'react';
import { usePaymentStatus } from './usePaymentStatus';

export const useSetupTariffFree = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [isError, setIsError] = useState(false);
  const paymentStatus = usePaymentStatus();

  async function handleClickBecomeFreeUser() {
    setIsLoading(true);
    const [deviceId, deviceIp, { userId }, { apiBaseUrl }] = await Promise.all([
      getDeviceId(),
      getDeviceIP(),
      getLocalStoragePremiumData([LocalStoragePremiumKey.UserId]),
      getSessionMetaApiData(),
    ]);
    try {
      const response = await setupTariffFree(apiBaseUrl, {
        deviceId,
        deviceIp,
        userId,
      });
      await setLocalStoragePremiumData({ hasPremium: false });
      await setLocalStoragePremiumData({
        status: response?.data?.subscription?.status || paymentStatus,
      });
    } catch (error) {
      console.error(error);
      setIsError(true);
    } finally {
      setIsLoading(false);
    }
  }

  return {
    handleClickBecomeFreeUser,
    isLoading,
    isError,
  };
};
