import {
  deleteLocalStorageData,
  getLocalStorageData,
  LocalStorageKey,
} from '@app/storage/local/shared';
import { useCallback, useEffect, useState } from 'react';

export function useErrorScreen() {
  const [errorMessage, setErrorMessage] = useState<string>('');

  const handleRetry = useCallback(async () => {
    await deleteLocalStorageData([LocalStorageKey.ErrorMessage]);
  }, []);

  useEffect(() => {
    async function getErrorData() {
      const { errorMessage } = await getLocalStorageData(
        LocalStorageKey.ErrorMessage,
      );
      if (errorMessage) {
        setErrorMessage(errorMessage);
      }
    }

    void getErrorData();
  }, []);

  useEffect(() => {
    const listener = async (changes: chrome.storage.StorageChange) => {
      const { errorMessage } = changes as {
        [LocalStorageKey.ErrorMessage]: { newValue: string };
      };

      if (errorMessage) {
        setErrorMessage(errorMessage.newValue);
      }
    };

    chrome.storage.local.onChanged.addListener(listener);

    return () => {
      chrome.storage.local.onChanged.removeListener(listener);
    };
  }, []);

  return {
    errorMessage,
    handleRetry,
  };
}
