import { Button } from '@popup/shared/components/Button';
import { useHandlePremiumWithoutTelegram } from '@popup/shared/hooks/useHandlePremiumWithoutTelegram';
import { forwardRef, useCallback } from 'react';
import { StepProps } from '../../types';
import { Separator } from '../Separator/Separator';
import { SaleLogo } from '../../../../shared/SaleLogo';
import styles from './SaleStep.module.css';
import { Trans, useTranslation } from 'react-i18next';

export const SaleStep = forwardRef<HTMLDivElement, StepProps>(
  ({ onStepChange }, ref) => {
    const { t } = useTranslation('youboost-neo', {
      keyPrefix: 'onboarding.sale-step',
    });

    const { handleClick, loading } = useHandlePremiumWithoutTelegram();

    const handleContinue = useCallback(() => {
      handleClick(true);
    }, [handleClick]);

    const handleCancel = useCallback(() => {
      onStepChange(3);
    }, [onStepChange]);

    return (
      <div className={styles.container} ref={ref}>
        <div className={styles.content}>
          <div className={styles.saleLogo}>
            <SaleLogo />
          </div>
          <div className={styles.saleTextContainer}>
            <h3 className={styles.saleTextTitle}>{t('title')}</h3>
            <p className={styles.saleTextDescription}>
              <Trans t={t} i18nKey="description" />
            </p>
          </div>
        </div>
        <div className={styles.buttons}>
          <Button
            className={styles.button}
            variant="primary"
            onClick={handleContinue}
            disabled={loading}
            loading={loading}
          >
            {t('go-premium')}
          </Button>
          <Separator />
          <Button
            className={styles.button}
            variant="secondary"
            onClick={handleCancel}
            disabled={loading}
          >
            {t('cancel')}
          </Button>
        </div>
      </div>
    );
  },
);

SaleStep.displayName = 'SaleStep';
