import { useEffect, useState } from 'react';
import {
  SessionStorageKey,
  getSessionStorageData,
} from '../../../../../app/storage/session/shared';

const version = chrome.runtime.getManifest().version;

const getNegativeFeedbackUrl = (
  proxy?: string,
  port?: number,
  userId?: string,
): string => {
  const body = [proxy, userId, version];

  return `${process.env.POPUP_HTML_FEEDBACK_BOT_URL}?start=${btoa(JSON.stringify(body))}`;
};
export function useNegativeFeedbackUrl(): { negativeFeedbackUrl: string } {
  const [proxy, setProxy] = useState<string | undefined>();
  const [port, setPort] = useState<number | undefined>();
  const [userId, setUserId] = useState<string | undefined>();

  useEffect(() => {
    async function getStatus(): Promise<void> {
      const { lastProxyHost, lastProxyPort } = await getSessionStorageData([
        SessionStorageKey.LastProxyHost,
        SessionStorageKey.LastProxyPort,
      ]);
      setProxy(lastProxyHost);
      setPort(lastProxyPort);

      const { LocalStoragePremiumKey, getLocalStoragePremiumData } =
        await import('../../../../../app/storage/local/premium');
      const { userId } = await getLocalStoragePremiumData(
        LocalStoragePremiumKey.UserId,
      );
      setUserId(userId);
    }
    void getStatus();
  }, []);

  useEffect(() => {
    function listener(changes: {
      lastProxyHost?: { newValue: string };
      lastProxyPort?: { newValue: number };
    }): void {
      if (changes.lastProxyHost) {
        setProxy(changes.lastProxyHost.newValue);
      }
      if (changes.lastProxyPort) {
        setPort(changes.lastProxyPort.newValue);
      }
    }
    chrome.storage.session.onChanged.addListener(listener);
    return () => {
      chrome.storage.session.onChanged.removeListener(listener);
    };
  }, []);

  return {
    negativeFeedbackUrl: getNegativeFeedbackUrl(proxy, port, userId),
  };
}
