import { XIcon } from '@popup/shared/icons/XIcon';
import styles from './CloseButton.module.css';
import clsx from 'clsx';

type CloseButtonProps = {
  fixed?: boolean;
  onClick: () => void;
};

export function CloseButton({ fixed = false, onClick }: CloseButtonProps) {
  return (
    <div
      className={clsx(styles.close, {
        [styles.fixed]: fixed,
      })}
      onClick={onClick}
    >
      <XIcon />
    </div>
  );
}
