import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  type PopupReadyMessage,
  type Message,
} from '../../../../app/messages/shared';
import { Actions } from '../../../../app/actions/shared';
import { RoutePath } from '../routes/types/RoutePath';

export function useNavigateToPremiumInfo(): void {
  const navigate = useNavigate();

  useEffect(() => {
    chrome.runtime.onMessage.addListener((message: Message) => {
      if (message.action === Actions.NavigateToFreeTrial) {
        navigate(RoutePath.PremiumInfo);
      }
    });
  }, [navigate]);

  useEffect(() => {
    void chrome.runtime.sendMessage<PopupReadyMessage>({
      action: Actions.PopupReady,
    });
  });
}
