import { Button } from '@popup/shared/components/Button';
import { useHandlePremiumWithoutTelegram } from '@popup/shared/hooks/useHandlePremiumWithoutTelegram';
import { forwardRef, useCallback } from 'react';
import { StepProps } from '../../types';
import { Separator } from '../Separator/Separator';
import { GuestLogo } from './components/GuestLogo/GuestLogo';
import styles from './GuestStep.module.css';
import { AdvCard } from '../AdvCard/AdvCard';
import { AdvCards } from '../AdvCard/types/AdvCards';
import { SDIcon } from '@popup/shared/icons/SDIcon';
import { BadConnectionIcon } from '@popup/shared/icons/BadConnectionIcon';
import { SlowLoadIcon } from '@popup/shared/icons/SlowLoadIcon';
import { useTranslation } from 'react-i18next';

const CARDS: AdvCards[] = [
  {
    titleLocaleKey: 'onboarding.guest-step.advantage-cards.1',
    image: SlowLoadIcon,
    size: 'wide',
    type: 'warn',
  },
  {
    titleLocaleKey: 'onboarding.guest-step.advantage-cards.2',
    image: BadConnectionIcon,
    size: 'wide',
    type: 'warn',
  },
  {
    titleLocaleKey: 'onboarding.guest-step.advantage-cards.3',
    image: SDIcon,
    size: 'wide',
    type: 'warn',
  },
];

export const GuestStep = forwardRef<HTMLDivElement, StepProps>(
  ({ onStepChange }, ref) => {
    const { t } = useTranslation('youboost-neo', {
      keyPrefix: 'onboarding.guest-step',
    });

    const { handleClick, loading } = useHandlePremiumWithoutTelegram();

    const handleContinuePremium = useCallback(() => {
      handleClick(true);
    }, [handleClick]);

    const handleContinueGuest = useCallback(() => {
      handleClick(false, true);
    }, [handleClick]);

    return (
      <div className={styles.container} ref={ref}>
        <div className={styles.content}>
          <div className={styles.logo}>
            <GuestLogo />
          </div>
          <div className={styles.textContainer}>
            <h3 className={styles.textTitle}>{t('title')}</h3>
          </div>
          <div className={styles.cards}>
            {CARDS.map((card) => (
              <AdvCard key={card.titleLocaleKey} card={card} />
            ))}
          </div>
        </div>
        <div className={styles.buttons}>
          <Button
            className={styles.button}
            variant="primary"
            onClick={handleContinuePremium}
            disabled={loading}
            loading={loading}
          >
            {t('go-premium')}
            <span className={styles.discount}>{t('premium-discount')}</span>
          </Button>
          <Separator />
          <Button
            className={styles.button}
            variant="secondary"
            onClick={handleContinueGuest}
            disabled={loading}
          >
            {t('go-guest-mode')}
          </Button>
        </div>
      </div>
    );
  },
);

GuestStep.displayName = 'GuestStep';
