import { Button } from '@popup/shared/components/Button';
import { useHandlePremiumWithoutTelegram } from '@popup/shared/hooks/useHandlePremiumWithoutTelegram';
import { FourKIcon } from '@popup/shared/icons/FourKIcon';
import { HeadphoneIcon } from '@popup/shared/icons/HeadphoneIcon';
import { WebIcon } from '@popup/shared/icons/WebIcon';
import { YouTubeIcon } from '@popup/shared/icons/YouTubeIcon';
import { BrowsersIcon } from '@popup/shared/icons/BrowsersIcon';
import { forwardRef, useCallback } from 'react';
import { StepProps } from '../../types';
import { Separator } from '../Separator/Separator';
import { AdvCard } from '../AdvCard/AdvCard';
import { AdvCards } from '../AdvCard/types/AdvCards';
import styles from './PremiumStep.module.css';
import { useTranslation } from 'react-i18next';

const CARDS: AdvCards[] = [
  {
    titleLocaleKey: 'onboarding.premium-step.advantage-cards.1',
    image: WebIcon,
    size: 'wide',
    type: 'success',
  },
  {
    titleLocaleKey: 'onboarding.premium-step.advantage-cards.2',
    image: FourKIcon,
    size: 'wide',
    type: 'success',
  },
  {
    titleLocaleKey: 'onboarding.premium-step.advantage-cards.3',
    image: BrowsersIcon,
    size: 'wide',
    type: 'success',
  },
  {
    titleLocaleKey: 'onboarding.premium-step.advantage-cards.4',
    image: YouTubeIcon,
    size: 'wide',
    type: 'success',
  },
  {
    titleLocaleKey: 'onboarding.premium-step.advantage-cards.5',
    image: HeadphoneIcon,
    size: 'wide',
    type: 'success',
  },
];

export const PremiumStep = forwardRef<HTMLDivElement, StepProps>(
  ({ onStepChange }, ref) => {
    const { t } = useTranslation('youboost-neo', {
      keyPrefix: 'onboarding.premium-step',
    });

    const { handleClick, loading } = useHandlePremiumWithoutTelegram();

    const handleContinuePremium = useCallback(() => {
      handleClick(false);
    }, [handleClick]);

    const handleContinueFree = useCallback(() => {
      onStepChange(2);
    }, [onStepChange]);

    return (
      <div className={styles.container} ref={ref}>
        <div className={styles.content}>
          <div className={styles.cards}>
            {CARDS.map((card) => (
              <AdvCard key={card.titleLocaleKey} card={card} />
            ))}
          </div>
        </div>
        <div className={styles.buttons}>
          <Button
            className={styles.button}
            variant="primary"
            onClick={handleContinuePremium}
            disabled={loading}
            loading={loading}
          >
            {t('go-premium')}
          </Button>
          <Separator />
          <Button
            className={styles.button}
            variant="secondary"
            onClick={handleContinueFree}
            disabled={loading}
          >
            {t('go-free')}
          </Button>
        </div>
      </div>
    );
  },
);

PremiumStep.displayName = 'PremiumStep';
