import { forwardRef, useCallback } from 'react';
import { Button } from '@popup/shared/components/Button';
import { useHandlePremiumWithoutTelegram } from '@popup/shared/hooks/useHandlePremiumWithoutTelegram';
import { HeadphoneIcon } from '@popup/shared/icons/HeadphoneIcon';
import { YouTubeIcon } from '@popup/shared/icons/YouTubeIcon';
import { StepProps } from '../../types';
import { CloseButton } from '../CloseButton/CloseButton';
import { Separator } from '../Separator/Separator';
import { SDIcon } from '@popup/shared/icons/SDIcon';
import { AdvIcon } from '@popup/shared/icons/AdvIcon';
import { KeyIcon } from '@popup/shared/icons/KeyIcon';
import { BrowserIcon } from '@popup/shared/icons/BrowserIcon';
import { AdvCards } from '../AdvCard/types/AdvCards';
import { AdvCard } from '../AdvCard/AdvCard';
import styles from './FreeInfoStep.module.css';
import { useTranslation } from 'react-i18next';

const CARDS: AdvCards[] = [
  {
    titleLocaleKey: 'onboarding.free-info-step.advantage-cards.1',
    image: YouTubeIcon,
    size: 'big',
    type: 'unsuccess',
  },
  {
    titleLocaleKey: 'onboarding.free-info-step.advantage-cards.2',
    image: SDIcon,
    size: 'big',
    type: 'unsuccess',
  },
  {
    titleLocaleKey: 'onboarding.free-info-step.advantage-cards.3',
    image: AdvIcon,
    size: 'big',
    type: 'unsuccess',
  },
  {
    titleLocaleKey: 'onboarding.free-info-step.advantage-cards.4',
    image: HeadphoneIcon,
    size: 'big',
    type: 'unsuccess',
  },
  {
    titleLocaleKey: 'onboarding.free-info-step.advantage-cards.5',
    image: KeyIcon,
    size: 'big',
    type: 'unsuccess',
  },
  {
    titleLocaleKey: 'onboarding.free-info-step.advantage-cards.6',
    image: BrowserIcon,
    size: 'big',
    type: 'unsuccess',
  },
];

export const FreeInfoStep = forwardRef<HTMLDivElement, StepProps>(
  ({ onStepChange }, ref) => {
    const { t } = useTranslation('youboost-neo', {
      keyPrefix: 'onboarding.free-info-step',
    });

    const { handleClick, loading } = useHandlePremiumWithoutTelegram();

    const handleContinuePremium = useCallback(() => {
      handleClick(false);
    }, [handleClick]);

    const handleContinueFree = useCallback(() => {
      onStepChange(5);
    }, [onStepChange]);

    return (
      <div className={styles.container} ref={ref}>
        <div className={styles.content}>
          <div className={styles.cards}>
            {CARDS.map((card) => (
              <AdvCard key={card.titleLocaleKey} card={card} />
            ))}
          </div>
        </div>

        <div className={styles.buttons}>
          <Button
            className={styles.button}
            variant="primary"
            onClick={handleContinuePremium}
            disabled={loading}
            loading={loading}
          >
            {t('go-premium')}
          </Button>
          <Separator />
          <Button
            className={styles.button}
            variant="secondary"
            onClick={handleContinueFree}
            disabled={loading}
          >
            {t('go-free')}
          </Button>
        </div>
      </div>
    );
  },
);

FreeInfoStep.displayName = 'FreeInfoStep';
