import { HTTPError } from 'ky';
import { httpClient } from '../httpClient';
import {
  getSubscriptionApiUrl,
  type GetSubscriptionResponse,
  getCheckTariffApiUrl,
  type CheckTariffResponse,
  getSetupTariffApiUrl,
  type SetupTariffResponse,
} from './urls';
import { type Tariff } from './types/Tariff';

export async function fetchCheckTariffAvailability(
  apiBaseUrl: string,
  {
    userId,
    deviceId,
    deviceIp,
    tariff,
  }: {
    userId: string;
    deviceId: string;
    deviceIp?: string;
    tariff: Tariff;
  },
): Promise<CheckTariffResponse> {
  const checkTariffUrl = getCheckTariffApiUrl(apiBaseUrl);
  const data = await httpClient
    .post<CheckTariffResponse>(checkTariffUrl, {
      json: {
        userId,
        deviceId,
        deviceIp,
        tariff,
      },
    })
    .json();
  return data;
}

export async function setupTariff(
  apiBaseUrl: string,
  {
    userId,
    deviceId,
    deviceIp,
    tariff,
  }: {
    userId: string;
    deviceId: string;
    deviceIp?: string;
    tariff: Tariff;
  },
): Promise<SetupTariffResponse> {
  try {
    const setupTariffUrl = getSetupTariffApiUrl(apiBaseUrl);

    const data = await httpClient
      .post<SetupTariffResponse>(setupTariffUrl, {
        json: {
          userId,
          deviceId,
          deviceIp,
          tariff,
        },
      })
      .json();
    return data;
  } catch (error) {
    if (error instanceof HTTPError) {
      const errorData = await error.response.json();

      if (errorData?.errors?.length > 0) {
        const errorMessage = errorData.errors
          .map((error: { description?: string }) => error.description)
          .filter(Boolean)
          .join(', ');

        if (errorMessage.length > 0) {
          throw new Error(errorMessage);
        }
      }
    }
    throw error;
  }
}

type SubscriptionInfoHTTPError = {
  error: {
    code:
      | 'change_device'
      | 'user_required'
      | 'device_id_required'
      | 'user_not_found';
  };
  localized_message: string;
  parameter_errors: [
    {
      field: string;
      errors: [
        {
          code: string;
          localized_message: string;
        },
      ];
    },
  ];
};

export type SubscriptionInfoError = {
  error:
    | 'changeDevice'
    | 'userRequired'
    | 'deviceIdRequired'
    | 'userNotFound'
    | 'internal'
    | 'unknown'
    | 'requestBlockedError';
};

export async function fetchSubscriptionInfo(
  apiBaseUrl: string,
  {
    userId,
    deviceId,
    deviceIp,
  }: {
    userId?: string;
    deviceId?: string;
    deviceIp?: string;
  },
): Promise<GetSubscriptionResponse | SubscriptionInfoError> {
  try {
    const apiUrl = getSubscriptionApiUrl(apiBaseUrl);
    const data = await httpClient
      .post<GetSubscriptionResponse>(apiUrl, {
        json: {
          userId,
          deviceId,
          deviceIp,
        },
      })
      .json();

    return data;
  } catch (error) {
    console.error(error);
    if (error instanceof HTTPError) {
      if (error.response.status === 400) {
        const errorData =
          await error.response.json<SubscriptionInfoHTTPError>();
        if (errorData.error.code === 'change_device') {
          return { error: 'changeDevice' };
        }
        if (errorData.error.code === 'user_required') {
          return { error: 'userRequired' };
        }
        if (errorData.error.code === 'device_id_required') {
          return { error: 'deviceIdRequired' };
        }
        if (errorData.error.code === 'user_not_found') {
          return { error: 'userNotFound' };
        }
      }
      if (error.response.status === 500) {
        return { error: 'internal' };
      }
    }
    if (error instanceof Error && error.message === 'Failed to fetch') {
      return { error: 'requestBlockedError' };
    }
    return { error: 'unknown' };
  }
}
