import clsx from 'clsx';

import styles from './StepSwitcher.module.css';

type StepSwitcherProps = {
  currentStep: number;
  onSwitch: (step: number) => void;
  totalStepsCount: number;
  className?: string;
};

export function StepSwitcher({
  className,
  currentStep,
  onSwitch,
  totalStepsCount,
}: StepSwitcherProps): JSX.Element {
  const steps = Array.from({ length: totalStepsCount });

  return (
    <div className={clsx(styles.wrapper, className)}>
      {steps.map((_, index) => (
        <button
          key={index}
          className={clsx(
            styles.button,
            currentStep === index && styles.active,
          )}
          onClick={() => onSwitch(index)}
        ></button>
      ))}
    </div>
  );
}
